.sp-no-outline {
  outline: none;
}
.sp-drawer-mask {
  background-color: transparent !important;
}
.sp-comment-drawer-btn {
  font-size: 12px;
  font-weight: 500;
  letter-spacing: .007px;
  text-transform: uppercase;
  color: white !important;
  background: #2196F3 !important;
  /* padding: 10px 16px !important; */
